
#app {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  opacity: 1;
}
#app.spinning {
  opacity: 0.6;
}
#spinner {
  position: fixed;
  z-index: 1000;
  top: 20px;
  right: 16px;
}

.capitalize {
  text-transform: capitalize;
}
.navbar {
  font-size: 16px;
}
.navbar p {
  margin: 1em 0;
}

#themer[data-v-b7a89d5a] {
  opacity: 0.2;
  position: fixed;
  top: 75px;
  right: 80px;
}
#themer[data-v-b7a89d5a]:hover {
  opacity: 0.6;
}

h1[id]:before, h2[id]:before, h3[id]:before {
  display: block;
  content: " ";
  margin-top: -110px;
  height: 110px;
  visibility: hidden;
}
.main {
  padding: 1rem 1.5rem;
}
#write {
  max-width: 40em;
  margin: 0 auto;
  padding-top: 2em 60px;
  min-height: calc(100vh - 6em);
}
.sidebar {
  display: none;
  position: fixed;
  right: 0;
  top: 120px;
  bottom: 5px;
  width: 240px;
  margin: 0;
  font-size: 12px;
  border-left: 1px solid #eee;
  overflow-y: auto;
}
@media only screen
and (min-width : 1200px) {
.main.with-sidebar {
    padding-right: 60px;
}
.sidebar, #toc-button {
    display: block;
}
}
#back-top {
  margin: 10px 18px;
}
.toc ul {
  padding: 0 8px 0 20px;
  margin: 0;
}
.toc li {
  list-style: none;
  text-align: initial;
  margin-top: 6px;
  margin-bottom: 6px;
  line-height: 18px;
}
.toc a {
  color: inherit;
  text-align: left;
  text-decoration: none;
  font-size: 12px;
}
.toc > ul ul {
  padding-left: 0px;
}
.toc > ul ul ul {
  padding-left: 15px;
}
.toc > ul > li > a {
  font-size: 16px;
  opacity: 0.6;
}
.toc > ul ul a {
  font-size: 14px;
}
.toc > ul ul ul a {
  font-size: 12px;
}
